.rights_modal .modal-content{
    border-radius: 0;
    height: auto;
    border: none;
}

.rights_modal_sidebar{
    max-width: 720px;
    margin: 0 auto;
    width: 100%;
    height: auto;
    background-color: var(--bs-body-bg);
}

.rights_modal_inner {
    height: auto;
    width: 100%;
}

.rights_modal_img_wrapper .object-center-cover{
    object-fit:contain;
}

.rights_modal_arrow_right,
.rights_modal_arrow_left{
    height: 38px;
    width: 38px;
    opacity: .7;
}

.rights_modal_arrow_left{
    left: 6%;
}

.rights_modal_arrow_right{
    right: 6%;
}

.rights_account_row{
    margin-left: -0.25rem !important;
    margin-right: -0.25rem !important;
}

input[type="file"] {
    display: none;
}
.custom-file-upload {
    border: 1px solid #ccc;
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
}

.rights_modal_img_wrapper{
    max-width: 500px;
    margin: 0 auto;
}

.rights_modal_img_wrapper .carousel-indicators{
    overflow: auto;
    justify-content: space-between;
}

.rights_modal_img_wrapper .carousel-control-prev, 
.rights_modal_img_wrapper .carousel-control-next {
    height: auto;
    align-items: flex-start;
    justify-content: flex-start;
    width: auto;
    opacity: 1;
    top: 30%;
}    
.rights_modal_img_wrapper .carousel-control-next {
    right: 0%;
}
.rights_modal_img_wrapper .carousel-control-prev {
    left: 0%;
}
@media (min-width: 992px){
    .rights_modal_sidebar{
        max-width: 460px;
        min-height: calc(100vh - 46px );
        padding-bottom: 72px;
        border-left: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color) !important;
    }
    .rights_modal_inner {
        width: calc(100% - 460px);
        height: calc(100vh - 46px);
    }

    .rights_modal .modal-content{
        min-height: 100vh;
        height: auto;
        background-color: rgb(var(--bs-body-bg-rgb));
    }
    .assets_m_header_sidebar{
        width: 100%;
    }
    .rights_modal_img_wrapper{
        height: 100%;
    }
    .rights_modal_inner {
        height: calc(100vh - 46px);
    }
    .rights_modal_arrow_right,
    .rights_modal_arrow_left{
        height: 50px;
        width: 50px;
    }
    .rights_modal_aside_wrap{
        min-height: calc(100vh - 118px);
    }
    .rights_modal_actions{
        position: absolute;
        bottom: 0;
    }
}

@media (min-width: 1280px){
    .rights_modal_img_wrapper .carousel-control-next {
        right: -20%;
    }
    .rights_modal_img_wrapper .carousel-control-prev {
        left: -20%;
    }
}

@media (min-width: 1600px){
    .rights_modal_sidebar{
        min-height: calc(100vh - 46px);
        max-width: 500px;

    }
    .rights_modal_inner {
        height: auto;
        min-height: calc(100vh - 46px);
        width: calc(100% - 500px);
    }
}