
.addSourceNetwork {
    width: 100%;
}


.uploadedAssetsGrid{
    flex: 0 0 auto;
    width: 50%;
}

.addSourceNetwork,
.innerModalAside {
    width: 100%;
}

.assetsModalScroll{
    height: calc(100vh - 45px);
    overflow-y: auto;
}
.uploadedAssetsGrid{
    width: 50%;
}
@media (min-width: 576px){
    .uploadedAssetsGrid{
        width: 33.33%;
    }
}

@media (min-width: 768px){
    .addSource .uppy-Dashboard-inner .uppy-Dashboard-innerWrap{
        height: 440px;
    }
    .addSourceNetworkList{
        height: 700px;
    }
    .uploadedAssetsGrid{
        width: 20%;
    }
}

@media (min-width: 991px){
    .addSourceNetwork,
    .innerModalAside {
        width: 320px;
    }

    .innerModalAside{
        position: sticky;
        top: 0;
    }
    .assetsModalScroll{
        height: calc(100vh - 46px);
    }
    .innerModalAsideWrapper {
        height: calc(100vh - 180px);
    }
    .uploadedAssetsGrid{
        width: 20%;
    }
}

@media (min-width: 1280px){
    .addSource .uppy-Dashboard-inner .uppy-Dashboard-innerWrap{
        height: 500px;
    }
    .uploadedAssetsGrid{
        width: 14.2857%;
    }
    
}
@media (min-width: 1600px){
    .uploadedAssetsGrid{
        width: 12.5%;
    }
    .addSourceNetwork,
    .innerModalAside {
        width: 360px;
    }
}