.assets_modal .modal-content{
    border-radius: 0;
    height: auto;
    border: none;
}

.full_modal_sidebar{
    max-width: 100%;
    width: 100%;
    height: auto;
    overflow-y: auto;
    background-color: var(--bs-body-bg);
}

.assets_modal_inner {
    height: auto;
}
.assets_modal_img_outer{
    height: auto;
    width: 100%;
}

.assets_modal_img_wrapper{
    width: 100%;
    position: relative;
}
.assets_modal_media__{
    max-width: 100%;
    height: auto;
    display: block;
    cursor: zoom-in;
}
.assets_modal_arrow_right,
.assets_modal_arrow_left{
    height: 38px;
    width: 38px;
    opacity: .7;
    z-index: 99;
}

.assets_modal_arrow_left{
    left: 6%;
}

.assets_modal_arrow_right{
    right: 6%;
}

.tab_product_dropdown_list{
    max-height: 270px;
    overflow-x: auto;
}

