.auth_bg{
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.auth_form_area{
    max-width: 450px;
    margin: 0 auto;
    width: 100%;
}

.view_pass{
    right: -10px;
}

@media (min-width: 992px){
    .auth_form_area{
        max-width: 470px;
    }
}

@media (min-width: 1600px){
    .auth_form_area{
        max-width: 500px;
    }
    .auth_form_area .form-control{    
        font-size: 14px;
        min-height: 44px;
    }
    .auth_form_area .btn-primary{    
        padding: 10px 16px;
        font-size: 14px;
    }
}