
.symbol-editMedia{
    height: 80px;
    width: 80px;
}

.editFilterEffect{
    position: relative;
}

.editFilterEffect .editableOption{
    opacity: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 0;
    right: 0;
    top: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    transition: .3s ease-in;
    background-color: rgba(var(--bs-light-rgb), .7);
}

.editFilterEffect:hover .editableOption, .editFilterEffect.active .editableOption{
    opacity: 1;
}

.media_filter_panel_ {
    max-width: 650px;
    margin: 0 auto;
    overflow-y: auto;
    padding-bottom: 5px;
}


.symbol-editMedia, .transform_bg{
    height: 100px;
    width: 100px;
}

@media (min-width: 992px) {
.editModalAside .nav-link {
    min-height: 90px;
}

.editMedia_modal_img_wrapper img{
    max-height: calc(100vh - 220px);
    min-height: 300px;
}

}

@media (max-width: 991px) {

    .editMediaSmallPenal {
        background-color: rgba(var(--bs-light-rgb), 1);
        width: 100%;
        margin-top: 30px;
        border-top-left-radius: 16px;
        border-top-right-radius: 16px;
        
       // display: none;
    }

    .editMediaSmallPenal.active {
        display: block;
    }
    .editMedia_modal_inner {    
        justify-content: space-around;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        height: calc(100vh - 45px);
        overflow-x: auto;
    }
    .editMedia_modal_img_inner {
        height: calc(100vh - 330px);
        overflow: hidden;
    }

}

@media (min-width: 992px){
    .editMedia_modal_inner{
        height: calc(100vh - 46px);
    }

}