.gridView svg {
    max-width: 100%;
    margin: 0 auto;
    display: block;
    max-height: 140px;
}

.emailcapaignwrapper{
    max-width: 800px;
}
.emailcapaignArrow .svg-icon svg {
    width: 40px;
    height: 60px;
}
.emailCampaignMedia{
    height: 400px;
    overflow-y: auto;
}

.emailcapaignPreview {
    width: 350px;
}

@media (max-width: 991px) {
    .emailcapaignArrow .svg-icon {
        transform: rotate(90deg);
    }
    .emailcapaignArrow .svg-icon svg {
        width: 20px;
        height: 40px;
    }
}

@media (max-width: 575px) {
.gridView svg {
    max-height: 100px;
}
}