/* ------------------------------------------------
  [3] Size
------------------------------------------------- */
.uppy-Root [role~=tooltip][data-microtip-size=small]::after {
  width: 80px;
  white-space: initial;
}

.uppy-Root [role~=tooltip][data-microtip-size=medium]::after {
