.syncPro_modal_wrapper{
    display: flex;
    align-items: flex-start;
}

.signInWithBrand {
    background-color: var(--bs-light);
    border-radius: 2px;
    padding: 1px;
    width: 180px;
}

.signIn_txt{
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: center;
    width: 100%;
}

.signInWithBrandIcon{
    background-color: var(--bs-text-light);
    border-radius: 2px;
    justify-content: center;
}

.signInWithBrandIcon img {
    height: 20px;
    width: 20px;
}

.syncPro_modal_wrapper > div:first-child {
    width: 170px;
}

.signIn-Google {
    background-color: #4285f4;
}
.signIn-Google:hover,
.signIn-Google:focus,
.btn.signIn-Google:active,
.btn.signIn-Google:first-child:active{
    background-color: #4285f4;
    box-shadow: 0 0 5px #4285f4;
}

.signIn-Shopify {
    background-color: #86c128;
}
.signIn-Shopify:hover,
.signIn-Shopify:focus,
.btn.signIn-Shopify:active,
.btn.signIn-Shopify:first-child:active{
    background-color: #86c128;
    box-shadow: 0 0 5px #86c128;
}


.signIn-Bigcommerce {
    background-color: #34313f;
}
.signIn-Bigcommerce:hover,
.signIn-Bigcommerce:focus,
.btn.signIn-Bigcommerce:active,
.btn.signIn-Bigcommerce:first-child:active{
    background-color: #34313f;
    box-shadow: 0 0 5px #34313f;
}

.signIn-Magento {
    background-color: #ec6737;
}
.signIn-Magento:hover,
.signIn-Magento:focus,
.btn.signIn-Magento:active,
.btn.signIn-Magento:first-child:active{
    background-color: #ec6737;
    box-shadow: 0 0 5px #ec6737;
}

.syncPro_modal {
    padding-top: 10%;
    padding-bottom: 10%;
}

@media (max-width: 991px) {
    .syncPro_modal {
        padding-top: 2%;
        padding-bottom: 2%;
    }
}

@media (max-width: 575px) {
    .syncPro_modal_wrapper{
        width: 100%;
        flex-direction: column;
        align-items: stretch;
        border-bottom: 1px solid var(--bs-border-color);
        padding-bottom: 15px;
    }
    .syncPro_modal_wrapper:last-child{
        border-bottom: none;
    }

    .syncPro_modal {
        padding-top: 0;
        padding-bottom: 0;
    }
}