.socialAdsMedia{
    height: 480px;
    overflow-y: auto;
}

.adsRightArrow .svg-icon svg {
    width: 40px;
    height: 60px;
}

.adsImage{
    height: 300px;
}

.adsImage svg{
    height: 90px;
    width: 90px;
}

.socialAdsPreview{
    max-width: 400px;
    margin: 0 auto;
}

@media (max-width: 991px) {
.adsRightArrow .svg-icon {
    transform: rotate(90deg);
}
}