$primary: #313A53;
$secondary:#f5f8fa;
$success:#6AC259;
$info:#545454;
$warning:#ffc700;
$danger:#db4b20;
$light:#f5f8fa;
$dark:#2b2b2b;

$border-color-translucent: #dadce0;

$theme-colors: (
  "primary":    $primary,
  "secondary":  $secondary,
  "success":    $success,
  "info":       $info,
  "warning":    $warning,
  "danger":     $danger,
  "light":      $light,
  "dark":       $dark
);

$primary-color:   #313A53;


$border-radius:8px;

$prefix:  bs- !default;
$prefix: $variable-prefix !default;
  




$font-weight-lighter:   200;
$font-weight-light:     300;
$font-weight-normal:    400;
$font-weight-bold:      600;
$font-weight-bolder:    700;

$h1-font-size:  'calc(12px + 1.15vw + 1.15vh)';
$h2-font-size:  'calc(12px + 0.9vw + 0.9vh)';
$h3-font-size:  'calc(12px + 0.7vw + 0.7vh)';
$h4-font-size:  'calc(12px + 0.56vw + 0.56vh)';
$h5-font-size:  '18px';
$h6-font-size:  '16px';

$h6-font-weight:  '600';


$font-family-base : 'Manrope', sans-serif;



$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1280px,
  xxl: 1600px
);

$container-max-widths: (
  sm: 100%,
  md: 720px,
  lg: 960px,
  xl: 1220px,
  xxl: 1520px
);

