.card {
  --bs-card-spacer-y: 1rem;
  --bs-card-spacer-x: 1rem;
  --bs-card-title-spacer-y: 0.5rem;
  --bs-card-border-width: 1px;
  --bs-card-border-radius: 8px;
  --bs-card-box-shadow: ;
  --bs-card-inner-border-radius: 8px;
  --bs-card-cap-padding-y: 0.5rem;
  --bs-card-cap-padding-x: 1rem;
  --bs-card-cap-bg: rgba(0, 0, 0, 0.03);
  --bs-card-cap-color: ;
  --bs-card-height: ;
  --bs-card-color: --bs-dark-rgb;
  --bs-card-img-overlay-padding: 1rem;
  --bs-card-group-margin: 0.75rem;
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  height: var(--bs-card-height);
  word-wrap: break-word;
  background-color: var(--bs-custom-card-bg);
  background-clip: border-box;
  border: var(--bs-card-border-width) solid var(--bs-card-border-color);
  border-radius: var(--bs-card-border-radius);
}
.card-body {
  color: var(--bs-card-color);
}
.dropdown-menu {
  --bs-dropdown-zindex: 1000;
  --bs-dropdown-min-width: 9rem;
  --bs-dropdown-padding-x: 0.6rem;
  --bs-dropdown-padding-y: 0.6rem;
  --bs-dropdown-spacer: 0;
  --bs-dropdown-font-size: 13px;
  --bs-dropdown-color: var(--bs-gray-800);
  --bs-dropdown-bg:  var(--bs-body-bg);
  --bs-dropdown-border-color: var(--bs-border-color-translucent);
  --bs-dropdown-border-radius: 8px;
  --bs-dropdown-border-width: 0px;
  --bs-dropdown-inner-border-radius: 8px;
  --bs-dropdown-divider-bg: var(--bs-border-color-translucent);
  --bs-dropdown-divider-margin-y: 0.5rem;
  --bs-dropdown-box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  --bs-dropdown-link-color: var(--bs-gray-800);
  --bs-dropdown-link-hover-color: #313A53;
  --bs-dropdown-link-hover-bg: var(--bs-light);
  --bs-dropdown-link-active-color:  #313A53;
  --bs-dropdown-link-active-bg:  var(--bs-light);
  --bs-dropdown-link-disabled-color: var(--bs-text-muted);
  --bs-dropdown-item-padding-x: 1rem;
  --bs-dropdown-item-padding-y: 0.25rem;
  --bs-dropdown-header-color: #6c757d;
  --bs-dropdown-header-padding-x: 1rem;
  --bs-dropdown-header-padding-y: 0.5rem;
  --bs-menu-dropdown-box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
  position: absolute;
  z-index: var(--bs-dropdown-zindex);
  display: none;
  min-width: var(--bs-dropdown-min-width);
  padding: var(--bs-dropdown-padding-y) var(--bs-dropdown-padding-x);
  margin: 0;
  font-size: var(--bs-dropdown-font-size);
  color: var(--bs-dropdown-color);
  text-align: left;
  list-style: none;
  background-color: var(--bs-dropdown-bg);
  background-clip: padding-box;
  border: none;
  border-radius: var(--bs-dropdown-border-radius);
  box-shadow: var(--bs-menu-dropdown-box-shadow);
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}
.dropdown-menu.show{
  box-shadow: var(--bs-menu-dropdown-box-shadow);
}

.dropdown-item {
  font-weight: 500;
}
.offcanvas, .offcanvas-xxl, .offcanvas-xl, .offcanvas-lg, .offcanvas-md, .offcanvas-sm {
