  --bs-accordion-border-color: var(--bs-border-color);
  --bs-accordion-border-width: 0px;
  --bs-accordion-border-radius: 4px;
  --bs-accordion-inner-border-radius: 4px;
  --bs-accordion-btn-padding-x: 14px;
